import { EditLogisticRoutingModule } from './edit-logistic-routing.module';
import { EditLogisticComponent } from './edit-logistic.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/shared.module';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

@NgModule({
  imports: [
    CommonModule,
    EditLogisticRoutingModule,
    SharedModule,
    NgxDatatableModule,
    FormsModule,
    HttpModule
  ],
  declarations: [EditLogisticComponent]
})
export class EditLogisticModule { }



