    export interface City {
        Id: number;
        Name: string;
    }

    export interface Area {
        City: City;
        Id: number;
        Name: string;
        CityId: number;
        Latitude: number;
        Longitude: number;
    }

    export interface City2 {
        Id: number;
        Name: string;
    }

    export class LogisticsModel {
        Area: Area;
        City: City2;
        Id: number;
        ContactPersonName?: any;
        LogisticsName: string;
        Address: string;
        Latitude?: any;
        Longitude?: any;
        CityId?: number;
        AreaId?: number;
    }




