import { AddAreaService } from './../../services/AddAreaService';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';
import { Router, ActivatedRoute, Params } from '@angular/router';


import { Location } from '@angular/common';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { LogisticsModel } from '../../models/logitic.Model';
declare var jQuery: any;

@Component({
  selector: 'logistic',
  templateUrl: './edit-logistic.component.html',
  styleUrls: [
    './edit-logistic.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [AddAreaService, GetChemistDataService, MeasurementAndDosageSizesService]
})
export class EditLogisticComponent implements OnInit {

  public CompanyName: string = "";
  public ContactPersonName: string = "";

  public Address: string = "";
  public Email: string = "";
  public MobileNumber: number = 0;
  public City: number = 0;
  public Area: number = 0;


  public editingSaveBtn:boolean = false;

  public LogisticObject: any;
  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  rowsFilter = [];
  tempFilter = [];
  public runner: boolean = false;

  public editingLogistic: LogisticsModel;

  @ViewChild(DatatableComponent) logisticList: DatatableComponent;


  constructor(public activatedRoute: ActivatedRoute, public _AddAreaNameService: AddAreaService,
    public _getChemistDataService: GetChemistDataService, public _measurementAndDosageSizesService: MeasurementAndDosageSizesService) {

    this.editingLogistic = new LogisticsModel();

    this.getAllCities();


  }


  ngOnInit() {

    this.activatedRoute.queryParams.subscribe((params: Params) => {
      this.LogisticObject = params['id'];


      console.log(JSON.parse(localStorage.getItem("editLogistic")));

      this.editingLogistic = JSON.parse(localStorage.getItem("editLogistic"));
      this.getCityAreas();

    })

  }


  makeEditable(){
     this.editingSaveBtn = true;
     jQuery(".editable").prop("readonly",false);
  }
  

  onSelect({ selected }) {
    console.log(selected);
  }

  onActivate(event) { }

  updateFilter(event) {

    const val = event.target.value.toLowerCase();

    console.log(val);
    // filter our data
    const temp = this.tempFilter.filter(function (d) {

      return d.LogisticsName.toLowerCase().indexOf(val) !== -1 || !val;

    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.logisticList.offset = 0;

  }


  getAllCities() {

    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;
      });

  }// End of Get All Cities

  getCityAreasByThisCity(elem){
    this.editingLogistic.CityId = elem;
    this._AddAreaNameService.getAreaName(this.editingLogistic.CityId).subscribe(
      response => {
        this.areas_array = response.data;
      });

  }

  getAreaSelected(elem){
    this.editingLogistic.AreaId = elem;
    console.log(this.editingLogistic.CityId, this.editingLogistic.AreaId);
  }
  getCityAreas() {
    this._AddAreaNameService.getAreaName(this.editingLogistic.CityId).subscribe(
      response => {
        this.areas_array = response.data;
      });
  }// End of Get Areas of City


  updateLogistic() {
    var body = {};
    body = {
      CompanyName: this.editingLogistic.LogisticsName,
      ContactPersonName: this.editingLogistic.ContactPersonName,
/*       MobileNumber: this.editingLogistic.MobileNumber, */
      CityId: this.editingLogistic.CityId,
      AreaId: this.editingLogistic.AreaId,
      Address: this.editingLogistic.Address,
      UserRole: 5
    }
    console.log(body);

    this._measurementAndDosageSizesService.updateLogistic(body).subscribe(Response => {
      console.log(Response);
    });

  }

}



